body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.missing-part-row {
    display: flex;
    justify-items: center;
    width: 100%;
    margin: 0 auto;
}

.missing-part-row:nth-child(even) {
    background: #DDDDDD;
}


.missing-part-row .missing-part-column {    
    flex: 1 1 auto;
    text-align: center;
    width: 10%;
    /* font-size: 2.2rem; */
    font-weight: 600;
    padding: 1rem 0 1rem 0;
}


.missing-part-column-button {
    display: flex;
    /* width: 16.5%; */
    justify-content: center;
    justify-items: center;
}

.missing-part-container {
    
}

/* COLUMNS */

.mpc-part {
    font-size: 2.2rem;
    color: crimson;
}

.mpc-buffer {
    font-size: 2rem;
}

.mpc-module {
    font-size: 2rem;
}

.mpc-date {

}
.main-container {
    
}

.wrapper {    
    margin:  0 auto;    
    margin-top: 1.5%;
}

.group-id-title {
    text-align: center;
    margin-top: 2%;
    margin-bottom: 2%;
    font-size: 1.5rem;
    font-weight: bold;
}

.header {
    display: flex;
    margin: 0 auto;
    width: 100%;
    border: 1px black solid;
}

.header div {        
    display: flex;
    flex: 1 1 auto;
    text-align: center;
    justify-content: center;        
    width: 10%;
    background: black;
    color: blanchedalmond;
    font-size: 1.7rem;    
    padding: 0.7rem 0 0.7rem 0;
}

.header div:nth-child(5) {    
    /* width: 0.5%; */
    justify-content: center;
    justify-items: center;
}
.available-groups-container {
    margin: 0 auto;
    display: flex;    
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
    margin-top: 5%;
}

.group-title {
    display: flex;
    width: 100%;
    text-align: center;
    justify-content: center;    
    /* border: 1px solid #000; */
    font-size: 1.5rem;    
}

.groups-container-body {
    display: flex;
    /* border: 1px solid #000;    */
    width: 100%;
    flex-wrap: wrap;
}

.group-wrapper {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
}

.group-header {
    display: flex;    
    border: 1px solid #000;   
    width: 100%;
    font-size: 1.3rem;
}

.group-header-selected {
    background-color: dodgerblue;    
}

.group-station {
    display: flex;
    width: 5%;
    justify-content: center;
    margin: 1% 1% 1% 1%;
    padding: 0.5rem 0.5rem 0.5rem 0.5rem;        
    color: white;
    background: #000;
    border-radius: 10%;
    font-weight: bold;
}
.group-station-selected {    
    background: dodgerblue;
}

.group-select {
    display: flex;
    width: 25%;
    margin: 0 auto;
}

.config-container {
    color: black;    
}

.group-selection {
    display: flex;    
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 2%;
    text-align: center;
}

.group-select {
    display: flex;
    width: 25%;
    margin: 0 auto;
}


.edit-group-container {
    display: flex;
    flex-wrap: wrap;
}

.edit-group-button-container {
    display: flex;    
    width: 100%;
    flex: 1 1 auto;
    justify-content: center;
}

.edit-group-button {    
    width: 100%;
}

.edit-group-button-title {
    margin-top: 2%;    
    text-align: center;
}

.edit-group-button-element {
    display: flex;
    flex: 1 1 auto;
    justify-content: center;
    margin-top: 2%;
    
}

.edit-station-container {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    margin-top: 3%;
    border: 1px solid #000;
    justify-content: space-around;
}

.edit-station-button-title {
    width: 100%;    
    text-align: center;
    font-size: 1.2rem;    
}

.edit-station-select {    
    /* flex: 1 1 auto; */    
    border: 1px solid red;
    width: 20%;
    justify-content: space-evenly;
    align-content: center;
}

.edit-station-button-container {
    justify-content: space-evenly;
    display: flex;
    width: 20%;
}



