.todo-list {
    display: flex;
    width: 100%;
}
.corridor-map-container {
    width: 100%;       
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;    
}

.corridor {    
    margin: 0 auto;
    justify-items: center;
    width: 100%;    
    margin-top: 2%;
}

.corridor img {
    /* max-width: 750px; */
    max-width: 100%;
    margin-top: 5%;
    
}
body {
  margin: 0 auto;  
  background: lightyellow;
}

.app-bar {
  width: 100%;
}

.content-container {
  margin: 0 auto;
  width: 97%;
}

.not-selected {  
  cursor: default;
  color: black;
  text-decoration: none;
}

.selected {
  color: lightgray;
  pointer-events: none;
  text-decoration: none;
}
